import React , {useState} from 'react';
import getMenu from '../config/menuApi';
import {
    IonMenu,
    IonHeader,
    IonToolbar,
    IonTitle,
    IonContent,
    IonList,
    IonItem,
    IonRouterOutlet,
    IonLabel, IonMenuButton, useIonViewWillEnter
} from '@ionic/react';



const MenuItem = ({label, link, submenu}) => {
    if(submenu !== undefined) {
        return (
            <MenuList elements={submenu}/>
        );
    }
    return (
        <IonItem routerLink={link}>
            <IonLabel>
                {label}
            </IonLabel>
        </IonItem>
    );
}

const MenuList = ({elements}) => {
    return(
        <IonList>
            {elements.map((el, key) => (<MenuItem submenu={el.below} key={key} label={el.title} link={el.url}/>))}
        </IonList>
    );
}
const MainMenu = () => {
    const [items, setItems] = useState([]);
    useIonViewWillEnter(() => {
        getMenu().main.then(data => {
            setItems(data[0].items)
        }).catch(e => {
            console.log(e)
        })
    }, []);
    return (
        <>

            <IonMenu side="start" menuId="first" contentId="menuContent">
                <IonHeader>
                    <IonToolbar translucent color="primary">
                        <IonTitle>Menu</IonTitle>
                    </IonToolbar>
                </IonHeader>
                <IonContent >
                    <MenuList elements={items}/>
                </IonContent>
            </IonMenu>
            <IonRouterOutlet id="menuContent"/>
        </>
    );
}

export default MainMenu;
